import PluginsUtils from "../../utils/PluginsUtils";
import StandardStore from "../../stores/StandardStore";
import Theme from "../../components/theme/Theme";

const React = require("react");
const { Provider } = require("react-redux");
const PluginsContainer = require("../../components/plugins/PluginsContainer");

const EDITOR_WORKSPACE = process.env.MIX_EDITOR_WORKSPACE;
const plugins = require("../../workspaces/" + EDITOR_WORKSPACE + "/plugins");
const pluginsCfg = require("../../workspaces/" +
  EDITOR_WORKSPACE +
  "/pluginsConfig");
const ProjectUtils = require("../../utils/ProjectUtils");

const store = StandardStore(ProjectUtils.getEmptyProject(), {}, plugins);

const { withNamespaces } = require("react-i18next");

const getPluginsConfiguration = () => {
  return {
    standard: pluginsCfg.standard
      .map(plugin => ({
        name: plugin,
        cfg: {}
      }))
      .concat([])
  };
};

const { plugin2 } = require("../../workspaces/formular/theme.js");
const burgerLogo = require("../../assets/images/burger-logo1.png");

class Editor extends React.Component {
  render() {
    return (
      <Provider store={store}>
        <div className={plugin2.Body}>
          <img src={burgerLogo} alt="MyBurger" />
          <Theme path="/themes" version="no_version" />
          <PluginsContainer
            mode="standard"
            plugins={PluginsUtils.getPlugins(plugins)}
            pluginsConfig={getPluginsConfiguration()}
          />
        </div>
      </Provider>
    );
  }
}

module.exports = withNamespaces("translation")(Editor);



