const React = require("react");
const { connect } = require("react-redux");
const { createSelector } = require("reselect");
const PropTypes = require("prop-types");
const sidebarSelectors = require("./SideBar/store/selectors");
const { withNamespaces } = require("react-i18next");

class SideBar extends React.Component {
  static propTypes = {
    id: PropTypes.string,
    stateSelector: PropTypes.string
  };

  static defaultProps = {
    id: "main-sidebar",
    items: [],
    stateSelector: "sidebar"
  };

  render() {
    const { expanded, active, t } = this.props;
    return (
      <div id={this.props.id} className="sideBar">
        Expanded : {expanded}, Active : {active}
      </div>
    );
  }
}

const selector = createSelector(
  [
    sidebarSelectors.sideBarExpandedSelector,
    sidebarSelectors.sideBarActiveSelector
  ],
  (expanded, active) => {
    return {
      expanded: expanded ? 1 : 0,
      active: active ? 1 : 0,
      test: 123
    };
  }
);

const SideBarPlugin = connect(selector)(withNamespaces("sideBar")(SideBar));

// let's export the plugin and a set of required reducers
module.exports = {
  SideBar: SideBarPlugin,
  reducers: {
    sidebar: require("./SideBar/store/reducers")
  }
};



